<script>
export default {
  name: 'ScheduleList',
  props: ['schedule','index','totalsize','categorylist'],
  data: function(){
    return {
        isTopItem: true,
        isBottomItem: true,
        isDeletableItem: false,
    }
  },
  methods:{
    setItemState: function(){
        if(this.index == 0){
            this.isTopItem = true;
        }else{
            this.isTopItem = false;
        }
        if(this.index == this.totalsize - 1){
            this.isBottomItem = true;
        }else{
